import React, { useState } from 'react';
import {
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Button,
  Paper,
  Typography,
  TextField,
} from '@mui/material';
import axios from 'axios';

const LogsDisplay = ({ logs }) => (
  <Paper elevation={3} style={{ padding: '16px', marginTop: '16px', background: 'white' }}>
    <Typography variant="h5">Logs:</Typography>
    {Array.isArray(logs) && logs.length > 0 ? (
      <ul>
        {logs.map((log, index) => (
          <li key={index}>{log}</li>
        ))}
      </ul>
    ) : (
      <p>No logs found in the requested log file.</p>
    )}
  </Paper>
);
      
const InfoErrorLogs = () => {
  const [selectedOption, setSelectedOption] = useState('');
  const [numOccurrences, setNumOccurrences] = useState('');
  const [logs, setLogs] = useState([]);

  const handleChange = (event) => {
    setSelectedOption(event.target.value);
  };

  const handleNumOccurrencesChange = (event) => {
    const value = event.target.value;
    setNumOccurrences(value);
  };

  const handleFetchLogs = async () => {
    try {
      const occurrences = parseInt(numOccurrences, 10);

      if (isNaN(occurrences)) {
        // Handle the case where numOccurrences is not a valid integer
        console.error('Invalid value for numOccurrences:', numOccurrences);
        return;
      }

      const response = await axios.get(`/logs/logs?log_type=${selectedOption}&num_occurrences=${occurrences}`);
      setLogs(response.data.logs);
    } catch (error) {
      console.error('Error fetching logs:', error);
    }
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <FormControl fullWidth style={{ width: '130px', marginRight: '10px' }}>
          <InputLabel id="cmd-label">CMD</InputLabel>
          <Select
            labelId="cmd-label"
            id="cmd-select"
            value={selectedOption}
            label="CMD"
            onChange={handleChange}
          >
            <MenuItem value="info">Info</MenuItem>
            <MenuItem value="error">Error</MenuItem>
          </Select>
        </FormControl>
        <TextField
          type="input"
          label="Number"
          value={numOccurrences}
          onChange={handleNumOccurrencesChange}
          style={{  marginRight: '10px', width:'80px' }}
        />
        <Button onClick={handleFetchLogs} variant="contained" color="primary" >
          Fetch 
        </Button>
      </div>

      {/* Display logs */}
      {logs.length > 0 && <LogsDisplay logs={logs} style={{ marginTop: '16px' }} />}
    </div>
  );
};

export default InfoErrorLogs;

